/* 0. Mini SmartPhones ELEJE*/
@media (min-width: 120px) and (max-width: 319px) {

    .KulsoToogleButton {
        display: flex;
    }

    .ToggleButton {
        height: auto;
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        height: 2.2rem;
        width: 2.2rem;
        /*background: transparent;*/
        border: none;
        cursor: pointer;
        padding: 0;
        box-sizing: border-box;
       
    }
    .ToggleButton ul {
       /* color:#fa923f;*/
       color: rgba(40, 164, 241, 0.938);
    }
    
    /*Ezt csinálja ha bele, rákatintasz akár egér,billentyű*/
    .ToggleButton:focus {
       /* background-color:red;*/
           
        /*Eltvolítja a külső keretet ami megjelenik*/
        outline: none;
    }
    
    .ToggleButtonLine {
        width: 30px;
        height: 4px;
       /* background-color: #fa923f;
        background-color: rgba(40, 164, 241, 0.938);
        background-color:grey;
        background-color: orange;*/
        background-color: black;
    }
    


}

