/* 0. Mini SmartPhones ELEJE*/
@media (min-width: 120px) and (max-width: 319px) {  
    .NM_NavBar_egesz {
      border-bottom:1px solid rgba(40, 164, 241, 0.938);
      width:100%;
      height:10rem;
      display: flex;
      flex-direction: column;
      align-items: center;  
      /* border: 3px solid red;*/
      margin-top: 1%; 
      margin-bottom: 2%;
    }
  }
  /* 0. Mini SmartPhones VÉGE*/

/* 1. Most of the Smartphones Mobiles (Portrait) ELEJE */
@media (min-width: 320px) and (max-width: 480px) {
  
    .NM_NavBar_egesz {
        border-bottom:1px solid rgba(40, 164, 241, 0.938);
        width:100%;
        height:11rem;
        display: flex;
        flex-direction: column;
        align-items: flex-start;  
       /* border: 3px solid red;*/
        margin-top: 0.1rem; 
        position: relative;
        z-index: 2800;
    }    
  }

/* 1. Most of the Smartphones Mobiles (Portrait)  VÉGE*/


